/* Reset Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #FF6F61, #FFD700);
  color: #333;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 2.5em;
  color: white;
  margin: 20px 0;
  text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7);
  text-align: center;
}

/* Section Styles */
.section {
  width: 100%;
  max-width: 1200px;
  margin: 20px 0;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.section h2 {
  font-size: 1.8em;
  color: #FF6F61;
  margin-bottom: 15px;
  text-align: center;
}

/* Memes Section */
.memes {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.meme {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  text-align: center;
}

.meme img {
  width: 100%;
  height: auto;
}

.meme p {
  font-size: 1em;
  padding: 10px;
}

/* Deals Section */
.deals {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
}

.deal {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  text-align: center;
}

.deal img {
  width: 100%;
  max-width: 150px;
  height: auto;
  margin-bottom: 10px;
}

.deal h3 {
  font-size: 1.2em;
  color: #333;
}

.deal p {
  font-size: 1em;
  margin: 10px 0;
}

.deal button {
  background: #FF6F61;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.deal button:hover {
  background: #FF4C3B;
}

/* AI Section */
.ai-fun {
  text-align: center;
}

.ai-button {
  background: #FF6F61;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
  margin-top: 10px;
}

.ai-button:hover {
  background: #FF4C3B;
}

.joke {
  margin-top: 20px;
  font-size: 1.2em;
  font-style: italic;
  color: #333;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  h1 {
    font-size: 2em;
  }

  .deal {
    flex: 1 1 100%;
  }
}
